var { Delta } = require('rich-text');
var parseGroups = require('../../shared/delta/parse_groups');
var getOp = require('../../shared/delta/get_op');
var groupIsComplete = require('../../shared/delta/group_is_complete');
var groupItemsMatch = require('../../shared/delta/group_items_match');

export default function removeIncompleteGroups(delta) {
  // Chunk up the lines into contiguous groups of the same value
  var result = parseGroups(delta).reduce((result, group) => {
    // If the group is incomplete or one of the lines does not contain an image,
    // remove grouping from each line
    if (!groupIsComplete(group) || !allItemsMatch(delta, group)) {
      for (let line of group) {
        result = result.compose(removeGrouping(line.offset));
      }
    }

    return result;
  }, new Delta());

  return result.chop();
}

function allItemsMatch(delta, group) {
  return groupItemsMatch(group.map(line => getOp(delta, line.offset - 1).insert));
}

function removeGrouping(offset) {
  return new Delta().retain(offset).retain(1, { grouping: null });
}
