import Quill from 'quill';
import convertQuotes from '../../../../shared/delta/convert_quotes';

const QuillClipboard = Quill.import('modules/clipboard');

class Clipboard extends QuillClipboard {
  convert(html) {
    if (html) return super.convert(html);
    stripGoogleDocsWrapper(this.container);
    stripEmptyTags(this.container);
    convertEmDashes(this.container);
    const delta = super.convert();
    return convertQuotes(delta);
  }
}

// remove google docs wrapper tag, it confuses quill to have <span> wrap <p>
function stripGoogleDocsWrapper(el) {
  for (let child of el.children) {
    if (/^docs-internal-guid/.test(child.id)) {
      // Unwrap all children of firstChild
      while (child.firstChild) {
        child.parentNode.insertBefore(child.firstChild, child);
      }
      child.parentNode.removeChild(child);
    }
  }
}

// remove top-level empty tags that will cause unwanted empty paragraphs
function stripEmptyTags(el) {
  const children = Array.from(el.children);
  // eslint-disable-next-line no-restricted-syntax
  for (let i = 0; i < children.length; i++) {
    const child = children[i];
    if (isEmpty(child)) {
      el.removeChild(child);
      // Treat two consecutive blank lines as intentional
      // by skipping past the 2nd one
      i++;
    }
  }
}

// convert double dashes to em dashes
function convertEmDashes(el) {
  // the regex is a little more complicated than just '--' because we
  // only want to replace scenarios where two dashes are surrounded by non-dashes.
  // - group 1: match the start of a line or any non-dash character
  // - two dashes
  // - group 2: match the end of a line or any non-dash character
  el.innerHTML = el.innerHTML.replace(/(^|[^-])--($|[^-])/gm, '$1$2');
}

// return true if el has no children, or only a <br> tag
// iframes are allowed to be empty
function isEmpty(el) {
  if (el.tagName === 'IFRAME') {
    return false;
  } else if (el.childNodes.length === 0) {
    return true;
  } else if (el.childNodes.length === 1 && el.firstChild.tagName === 'BR') {
    return true;
  } else {
    return false;
  }
}

Quill.register('modules/clipboard', Clipboard, true);

export default Clipboard;
