var { Delta } = require('rich-text');
var FORMATS = require('../../config/schema/rich-text.json').attributes.properties;

module.exports = function(contents) {
  var delta = new Delta();
  var objectWasLast = false;

  for (let op of contents.ops) {
    if (typeof op.insert !== 'string') {
      delta.retain(1, allowlistContexts(op, ['object', 'any']));
      objectWasLast = true;
    } else if (/^\n+$/.test(op.insert)) {
      delta.retain(1, allowlistContexts(op, [objectWasLast ? 'object-line' : 'text-line', 'any-line', 'any']));
      objectWasLast = false;
    } else {
      delta.retain(op.insert.length, allowlistContexts(op, ['text', 'any']));
      objectWasLast = false;
    }
  }
  return delta;
};

function allowlistContexts(op, contexts) {
  var attrs = op.attributes || {};
  return Object.keys(attrs).reduce((memo, key) => {
    var format = FORMATS[key];
    if (!format || !contexts.includes(format.context)) {
      memo[key] = null;
    }
    return memo;
  }, {});
}
