import Quill from 'quill';

const Parchment = Quill.import('parchment');
const Block = Quill.import('blots/block');

class ListItem extends Block {
  static formats(domNode) {
    // LI does not correspond to a rich-text attribute
    return domNode.tagName === this.tagName ? undefined : super.formats(domNode);
  }

  format(name, value) {
    if ((name === 'bullet' || name === 'list') && !value) {
      // Replace with a default block when removing the bullet or list format
      this.replaceWith(Parchment.create(this.statics.scope));
    } else {
      super.format(name, value);
    }
  }

  remove() {
    if (this.prev == null && this.next == null) {
      // Remove the list when removing the last item
      this.parent.remove();
    } else {
      super.remove();
    }
  }

  replaceWith(name, value) {
    this.parent.isolate(this.offset(this.parent), this.length());
    if (name === this.parent.statics.blotName) {
      // Change from UL -> OL or vice versa by acting on the parent blot
      this.parent.replaceWith(name, value);
    } else {
      // Unwrap the UL when replacing the LI
      this.parent.unwrap();
      return super.replaceWith(name, value);
    }
  }
}

ListItem.blotName = 'list-item';
ListItem.tagName = 'LI';

Quill.register('formats/list-item', ListItem);

export default ListItem;
