var { intersection } = require('lodash');
var { Delta } = require('rich-text');

const REQUIRES_DEFAULT_POSITION = ['pullquote', 'aside', 'ratingcard'];

export default function setLineDefaults(delta) {
  var result = new Delta();

  for (let [i, currentOp] of delta.ops.entries()) {
    var currentAttr = currentOp.attributes || {};
    var previousInsert = i > 0 ? delta.ops[i - 1].insert : {};

    // A line is orphaned when it has a layout attribute (i.e. position or grouping)
    // and the previous op it references either:
    // A) Isn't a insert: 1 object op
    // B) Is an hr
    var isOrphaned = (
      (currentAttr.hasOwnProperty('position') || currentAttr.hasOwnProperty('grouping')) &&
      (typeof previousInsert === 'string' || previousInsert.hr)
    );

    // Checks to see if the object this line references in the previous op requires
    // the default `position: 'float-right'` attribute.
    var requiresDefaultPosition = (
      intersection(Object.keys(previousInsert), REQUIRES_DEFAULT_POSITION).length > 0 ||
      (previousInsert.doc && REQUIRES_DEFAULT_POSITION.indexOf(previousInsert.doc.type) > -1)
    );

    if (isOrphaned) {
      result.retain(1, { position: null, grouping: null });
    } else if (requiresDefaultPosition && !currentAttr.hasOwnProperty('position')) {
      result.retain(1, { position: 'float-right' });
    } else {
      result.retain(typeof currentOp.insert === 'string' ? currentOp.insert.length : 1);
    }
  }

  return result.chop();
}
