const autosave = require('../../../actions/autosave');

export default function() {
  const commentsClosed = app.state.get(['doc', 'head', 'contents', 'comments_closed']);

  if (app.state.get(['docLabel']) === 'custom story' && commentsClosed !== true) {
    autosave.localUpdate(app.state.get(['doc', 'id']), [{
      p: ['comments_closed'],
      oi: true
    }], -1);
  } else if (commentsClosed == null) {
    autosave.localUpdate(app.state.get(['doc', 'id']), [{
      p: ['comments_closed'],
      oi: app.state.get(['community', 'commentsClosedDefault'])
    }], -1);
  }
}
