import Quill from 'quill';
import LinkTooltip from './link_tooltip';
import AutolinkBlot from '../blots/autolink';

class AutolinkTooltip extends LinkTooltip {
  // Override to set href or the disabled attribute of the autolink format,
  // depending on the truthiness of the argument.
  format(href) {
    let range = this.quill.selection.savedRange;
    if (range.length === 0 && this.linkRange) {
      range = this.linkRange;
    }
    let op = this.quill.getContents(range).ops[0];
    if (!op || !op.attributes || !op.attributes.autolink) {
      return;
    }
    let value = Object.assign({}, op.attributes.autolink);
    if (href) {
      value.href = href;
    } else {
      value.disabled = true;
    }
    this.quill.formatText(range, 'autolink', value, 'user');
  }

  // Override for 3 reasons:
  // - Look for AutolinkBlot instead of LinkBlot
  // - Use `value.href` instead of `value` for the preview link
  // - Do not show the tooltip if the autolink is disabled
  onSelectionChange(range) {
    if (range == null) return;
    let [link, offset] = this.quill.scroll.descendant(AutolinkBlot, range.index);
    if (link != null && offset + range.length <= link.length()) {
      let value = AutolinkBlot.formats(link.domNode);
      if (value.disabled) {
        this.hide();
      } else {
        this.preview.textContent = value.href;
        this.preview.href = value.href;
        this.linkRange = { index: range.index - offset, length: link.length() };
        this.show();
      }
    } else {
      this.hide();
    }
  }
}

Quill.register('modules/autolink-tooltip', AutolinkTooltip);

export default AutolinkTooltip;
