var { Delta } = require('rich-text');
var getAllOffsets = require('../../../../shared/delta/get_all_offsets');
var autosave = require('../../../actions/autosave');
var hasMinimalLayout = require('../../../../shared/has_minimal_layout');

const FEATURE_ONLY = /(hang-left|hang-right|full-bleed|wide-block)/;

export default function initEntryTypeObserver() {
  var entryLayout = app.state.select(['doc', 'head', 'contents', 'draft', 'entry_layout']);
  entryLayout.on('update', () => {
    if (entryLayout.get() === 'unison_standard') {
      removeInvalidPositions();
    }
  });

  // only stories with a standard entry layout can have a lead gallery
  var mainGallery = app.state.select(['doc', 'head', 'contents', 'draft', 'main_gallery']);
  mainGallery.on('update', () => {
    var layout = entryLayout.get();
    if (mainGallery.get() && hasMinimalLayout(layout)) {
      autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'entry_layout'], od: layout, oi: 'unison_standard' }], -1);
    }
  });
}

// Loops through and removes any invalid position attributes
function removeInvalidPositions() {
  var body = app.state.get(['doc', 'head', 'contents', 'draft', 'body']);
  var invalids = getAllOffsets(body, op => {
    return op.attributes.hasOwnProperty('position') && FEATURE_ONLY.test(op.attributes.position);
  });

  if (invalids.length > 0) {
    var deltas = invalids.map(offset => {
      var delta = new Delta().retain(offset).retain(1, { position: null });
      return { p: ['draft', 'body'], t: 'rich-text', o: delta };
    });

    autosave.localUpdate(app.state.get(['doc', 'id']), deltas, -1);
  }
}
