var moment = require('moment');
var NotFound = require('../components/404.html');
var autosave = require('../actions/autosave');
var communityActions = require('../actions/communities');
var docActions = require('../actions/docs');
var entryActions = require('../actions/entry');
var CommunityMismatchError = require('../../shared/errors/community_mismatch');
var initAutosave = require('../initializers/editor/autosave');
var initQuill = require('../initializers/editor/quill');
var initUnsavedWarning = require('../initializers/editor/unsaved_warning');
var initReloadMessage = require('../initializers/editor/reload_tab');
var initUserCountWarning = require('../initializers/editor/user_count_warning');
var initCommunityChangedNotice = require('../initializers/editor/community_changed_notice');
var initCustomstories = require('../initializers/editor/doc/customstories');
var initEntryLayout = require('../initializers/editor/doc/entry_layout');
var initPackageStories = require('../initializers/editor/doc/package_stories');
var initStreamMembers = require('../initializers/editor/doc/stream_members');
var initComments = require('../initializers/defaults/doc/comments');
var initSlugIsEvergreenDefault = require('../initializers/defaults/doc/slug_is_evergreen_default');
var initCustomFields = require('../initializers/defaults/doc/custom_fields');
var setAutotweets = require('../initializers/defaults/doc/autotweet');
var setContributors = require('../initializers/defaults/doc/contributors');
var setStructuredTags = require('../initializers/defaults/doc/structured_tags');
var setLeadArt = require('../initializers/defaults/doc/lead_art');
var setGroups = require('../initializers/defaults/doc/groups');
var setDefaultLinkSet = require('../initializers/defaults/doc/link_sets');
var sentryActions = require('../actions/sentry');

export default function(ctx, next) {
  var uuid = ctx.pathname.slice(1).split('/', 2).pop();

  var currentDoc = app.state.get(['doc']);
  if (currentDoc != null && currentDoc.uuid === uuid) {
    // Skip initialization if we're already looking at the same doc
    return next();
  }

  // Order of operations is important:
  // 1. Initialize autosave
  // 2. Load the doc from the server
  // 3. Set it in state
  // 4. Load in community + entitlements, using the community ID associated with the doc
  // 5. Initialize the doc for autosave
  // 6. Run editor and contents initializers
  initAutosave();
  docActions.getDoc(uuid)
    .then(doc => autosave.setDoc(doc, ['doc']))
    .then(() => haltIfTrashed(next))
    .then(() => checkCommunityContexts())
    .then(() => communityActions.loadCommunity(app.state.get(['doc', 'community_id']), 'Editor'))
    .then(() => autosave.initDoc(app.state.get(['doc', 'id']), true))
    .then(() => {
      initEditor();
      return initContents();
    })
    .then(() => next())
    .catch(sentryActions.handleError);
}

// If the doc is marked as trashed over 30 days ago, simply render not found
function haltIfTrashed(next) {
  var trashedAt = app.state.get(['doc', 'head', 'contents', 'trashed_at']);
  var threshold = moment().subtract(30, 'days');
  if (trashedAt && moment(trashedAt) < threshold) {
    app.render(NotFound);
    return next();
  }
  return Promise.resolve();
}

// There's a chance that a doc community change action will fail half way thru,
// leading to a community mismatch b/t the SBN entry and the Content API doc. If
// that's the case we should throw a fatal error, prompting us to triage manually.
async function checkCommunityContexts() {
  const entry = await entryActions.getEntryById(app.state.get(['doc', 'entry_id']));

  if (entry && entry.community_id && app.state.get(['doc', 'community_id']) !== entry.community_id) {
    app.render(NotFound);
    throw new CommunityMismatchError(app.state.get(['doc']));
  }
}

// Runs editor-only initializers
function initEditor() {
  initQuill();
  initUnsavedWarning();
  initReloadMessage();
  initUserCountWarning();
  initCommunityChangedNotice();
}

function initContents() {
  setAutotweets();
  setContributors();
  setLeadArt();
  initEntryLayout();
  initComments();
  initSlugIsEvergreenDefault();
  initCustomFields();
  var docLabel = app.state.get(['docLabel']);
  if (docLabel !== 'page') {
    setGroups();
    setDefaultLinkSet();
    setStructuredTags();
  }
  if (docLabel === 'stream') {
    initStreamMembers();
  }
  if (docLabel === 'package') {
    return initPackageStories();
  }
  if (docLabel === 'custom story') {
    return initCustomstories();
  }

  return Promise.resolve();
}
