var { difference } = require('lodash');
var autosave = require('../../../actions/autosave');
var getPackageStories = require('../../../../shared/get_package_stories');
var docActions = require('../../../actions/docs');

const PACKAGE_LEAD_STORY_LAYOUT_KEYS = ['vertical', 'horizontal', 'fullwidth'];
const PACKAGE_STORY_LAYOUT_KEYS = ['1col', '2col'];

export default function initPackageStories() {
  var toc = app.state.select(['doc', 'head', 'contents', 'draft', 'package_toc']);
  toc.on('update', () => loadPackageStories(toc.get()));

  var coverComponents = app.state.select(['doc', 'head', 'contents', 'draft', 'package_cover']);
  coverComponents.on('update', () => setCoverStoryLayoutOptions(coverComponents.get()));
  setCoverStoryLayoutOptions(coverComponents.get());

  // package stories must be in state before the screen is loaded
  return loadPackageStories(toc.get());
}

function loadPackageStories(toc) {
  var oldStoryIds = Object.keys(app.state.get(['packageDocs']));
  var currentStoryIds = getPackageStories(toc).map(({ uuid }) => uuid);
  var removedStoryIds = difference(oldStoryIds, currentStoryIds);
  var addedStoryIds = difference(currentStoryIds, oldStoryIds);

  // Any edit to a TOC section heading will invoke this update handler. So, we
  // only want to add or remove package stories to state if the TOC stories list
  // itself has changed.
  //
  // NOTE: In an ideal world, we would compare the `currentData` and `previousData`
  // given to us by Baobab via the `toc.on('update')` handler above, only
  // invoking this function if the state has changed. But, due to a bug in our
  // current version of Baobab, `currentData` and `previousData` are always the
  // same. Thus, we compare manually with help from `_.difference`.
  if (removedStoryIds.length === 0 && addedStoryIds.length === 0) {
    return;
  }

  // Add new stories to state and then teardown stories that have been removed
  var promise = addedStoryIds.length > 0 ? setPackageStories(addedStoryIds) : Promise.resolve();
  return promise.then(() => {
    for (let uuid of removedStoryIds) {
      var docId = app.state.get(['packageDocs', uuid, 'id']);
      autosave.teardownDoc(docId);
    }
  });
}

function setPackageStories(uuids) {
  return docActions.searchDocs({
    uuids: uuids.join(',')
  }).then(({ docs }) => {
    // initialize new docs to receive their remote updates
    return Promise.all(docs.map(doc => {
      return autosave.setAndInitDoc(doc);
    }));
  });
}

function setCoverStoryLayoutOptions(coverComponents) {
  var docId = app.state.get(['doc', 'id']);
  for (let [index, component] of coverComponents.entries()) {
    if (!component.story) {
      continue;
    }
    // if the lead story has a non-lead story layout, reset it to `horizontal`
    // otherwise, a non-lead story has a lead story layout, reset it to `1col`
    var layout;
    if (index === 0 && PACKAGE_STORY_LAYOUT_KEYS.includes(component.story.layout)) {
      layout = 'horizontal';
    } else if (index > 0 && PACKAGE_LEAD_STORY_LAYOUT_KEYS.includes(component.story.layout)) {
      layout = '1col';
    }
    if (layout) {
      autosave.localUpdate(docId, [{
        p: ['draft', 'package_cover', index, 'story', 'layout'],
        od: component.story.layout,
        oi: layout
      }], -1);
    }
  }
}
