var autosave = require('../../../actions/autosave');

export default function() {
  var contents = app.state.get(['doc', 'head', 'contents']);
  var mediaType = contents.main_media_type;
  var delta = [];

  if (mediaType) {
    delta.push({
      p: ['main_media_type'],
      od: mediaType
    });
  }

  if (mediaType && (mediaType === 'VIDEO' || mediaType === 'NONE')) {
    var mainImage = contents.main_image;
    var cardImage = contents.internal_image;

    delta = delta.concat([{
      p: ['main_image'],
      od: mainImage
    }, {
      p: ['internal_image'],
      od: cardImage,
      oi: mainImage
    }]);
  }

  if (delta.length > 0) {
    autosave.localUpdate(app.state.get(['doc', 'id']), delta, -1);
  }
}
