import Quill from 'quill';
import ListItem from './list_item';

const Parchment = Quill.import('parchment');
const Container = Quill.import('blots/container');

class List extends Container {
  // Implement the FormatBlot interface
  static formats(/* domNode */) {
    return true;
  }

  // Pass along format command to last child
  format(name, value) {
    if (this.children.length > 0) {
      this.children.tail.format(name, value);
    }
  }

  // We're a container that contributes formats
  formats() {
    return { [this.statics.blotName]: true };
  }

  insertBefore(childBlot, refBlot) {
    if (childBlot instanceof ListItem) {
      super.insertBefore(childBlot, refBlot);
    } else {
      // split the list in order to insert a non-list-item
      let index = refBlot == null ? this.length() : refBlot.offset(this);
      let after = this.split(index);
      after.parent.insertBefore(childBlot, after);
    }
  }

  optimize(context) {
    super.optimize(context);
    let next = this.next;
    if (next != null &&
        next.prev === this &&
        next.statics.blotName === this.statics.blotName) {
      // merge adjacent lists
      next.moveChildren(this);
      next.remove();
    }
  }

  replace(target) {
    if (target.statics.blotName !== this.statics.blotName) {
      // Wrap replacement in a ListItem if needed
      let item = Parchment.create(this.statics.defaultChild);
      target.moveChildren(item);
      this.appendChild(item);
    }
    super.replace(target);
  }
}

List.blotName = 'list';
List.scope = Parchment.Scope.BLOCK_BLOT;
List.tagName = 'OL';
List.defaultChild = 'list-item';
List.allowedChildren = [ListItem];

Quill.register('formats/list', List);

export default List;
