var messages = require('../../config/messages');
var messageActions = require('../../actions/messages');
var hasMessagedUsers = false;

export default function initUserCountWarning() {
  setTimeout(() => messageUsers(), 100);
  var users = app.state.select('users');
  users.on('update', messageUsers);
}

function messageUsers() {
  if (!hasMessagedUsers && app.state.get('users').length > 10) {
    hasMessagedUsers = true;
    messageActions.showMessage({ message: messages.TOO_MANY_EDITORS, type: 'cautionary' });
  }
}
