var Delta = require('rich-text').Delta;

module.exports = function getAllOps(delta, isValid) {
  var offset = 0;
  return delta.ops.reduce((foundOps, op) => {
    if (op.hasOwnProperty('attributes') && isValid(op)) {
      foundOps.push(offset);
    }
    offset += new Delta([op]).length();
    return foundOps;
  }, []);
};
