const { forEachRight, uniq } = require('lodash');
const autosave = require('../../../actions/autosave');
const groupActions = require('../../../actions/groups');
const isDraft = require('../../../lib/is_draft');
const { isCommunityPostAuthor } = require('../../../lib/authorization');

export default function initGroups() {
  initGroupsForType('site_group');

  if (app.state.get(['community', 'network', 'usesSuperGroups'])) {
    initGroupsForType('super_group');
  }
  if (app.state.get(['community', 'network', 'usesCampaignGroups'])) {
    initGroupsForType('campaign_group');
  }
}

async function initGroupsForType(type) {
  // Load all groups from sbn
  const groups = await groupActions.loadGroups(type);
  await sanitizeGroupData(type, groups);
  if (type === 'site_group') {
    // Set site_group data if empty after clearing out old group data
    setInitialSiteGroupData(groups);
  }
}

// Clear groups from state that are no longer available for the given community
// Denormalize hub_page_id, name, and slug if not present
// Set a default value for placement_slot based on the community default, if not present
async function sanitizeGroupData(type, groups) {
  const delta = [];
  const groupsPath = ['draft', `${type}s`];
  const primaryGroupIdPath = ['draft', `primary_${type}_id`];

  // Primary group
  const primaryGroupId = app.state.get(['doc', 'head', 'contents', ...primaryGroupIdPath]);
  if (primaryGroupId && !groups.find(({ id }) => id === primaryGroupId)) {
    delta.push({ p: primaryGroupIdPath, od: primaryGroupId });
  }

  // Traverse the groups backwards to delete nonexistent and duplicate groups in place
  const groupsInState = app.state.get(['doc', 'head', 'contents', ...groupsPath]);
  const groupsInStateIds = groupsInState.map(g => g.id);
  const hasDupes = groupsInStateIds.length !== uniq(groupsInStateIds).length;
  forEachRight(groupsInState, (group, index) => {
    const groupFromApi = groups.find(({ id }) => id === group.id);
    // Note that when looking for duplicates, we want our subset to give us everything "in front" of the current index.
    if (
      !groupFromApi ||
      (hasDupes && groupsInStateIds.slice(0, index).includes(group.id))
    ) {
      delta.push({ p: [...groupsPath, index], ld: group });
    } else {
      // Update denormalized data from API if needed
      for (let key of ['hub_page_id', 'name', 'slug']) {
        if (group[key] !== groupFromApi[key]) {
          delta.push({ p: [...groupsPath, index, key], od: group[key], oi: groupFromApi[key] });
        }
      }
      if (!group.hasOwnProperty('placement_slot')) {
        const unpinnedByDefault = app.state.get(['community', 'placeFirstAvailableDefault']);
        delta.push({ p: [...groupsPath, index, 'placement_slot'], oi: unpinnedByDefault ? null : -1 });
      }
    }
  });

  if (delta.length > 0) {
    await autosave.resolveOnLocalUpdate(app.state.get(['doc', 'id']), delta, -1);
  }
}

function setInitialSiteGroupData(groups) {
  const docIsDraft = isDraft(app.state.get(['doc', 'status']));
  const groupsInState = app.state.get(['doc', 'head', 'contents', 'draft', 'site_groups']);
  let defaultGroup;

  if (isCommunityPostAuthor()) {
    defaultGroup = groups.find(({ slug }) => slug === 'community');
  } else {
    defaultGroup = groups.find(({ slug }) => slug === app.state.get(['community', 'defaultGroupSlug']));
    if(!defaultGroup) {
      defaultGroup = groups.find(({ slug }) => slug === 'front-page');
    }
  }

  if (defaultGroup && docIsDraft && groupsInState.length === 0) {
    autosave.localUpdate(app.state.get(['doc', 'id']), [{
      p: ['draft', 'primary_site_group_id'],
      oi: defaultGroup.id
    }, {
      p: ['draft', 'site_groups', 0],
      li: groupActions.groupFromData(defaultGroup)
    }], -1);
  }
}
