import Quill from 'quill';

const Parchment = Quill.import('parchment');
const QuillHistory = Quill.import('modules/history');

class History extends QuillHistory {
  constructor(quill, options) {
    super(quill, options);

    this._undoOnBackspace = false;

    // Whenever a user-initiated edit or cursor movement happens, reset
    // _undoOnBackspace back to null
    quill.on('editor-change', (...args) => {
      let source = args.pop();
      if (source === 'user') {
        this._undoOnBackspace = false;
      }
    });

    // On backspace, undo() instead of the default behavior if requested
    // Use unshift() to ensure that our backspace key binding happens first
    quill.keyboard.bindings['Backspace'].unshift({ // eslint-disable-line dot-notation
      key: 'Backspace',
      collapsed: true,
      handler: () => {
        if (this._undoOnBackspace) {
          this.undo();
          return false;
        } else {
          return true;
        }
      }
    });
  }

  // Override
  // Use the manually recorded selection index, if present
  change(source, dest) {
    if (this.stack[source].length === 0) return;
    let delta = this.stack[source].pop();
    this.stack[dest].push(delta);
    this.lastRecorded = 0;
    this.ignoreChange = true;
    this.quill.updateContents(delta[source], Quill.sources.USER);
    this.ignoreChange = false;
    let index = (delta.index) ?
      delta.index[source] : getLastChangeIndex(delta[source]);
    this.quill.setSelection(index);
  }

  // Record selection indexes to set when undoing / redoing the last recorded change
  // (used to manually manipulate the selection for inline shortcuts)
  recordSelection(undoIndex, redoIndex = undoIndex) {
    let delta = this.stack.undo[this.stack.undo.length - 1];
    if (delta) {
      delta.index = { undo: undoIndex, redo: redoIndex };
    }
  }

  // Override
  // Ensure that the undoIndex and redoIndex are transformed as well
  transform(delta) {
    super.transform(delta);
    for (let change of this.stack.undo) {
      if (change.index) {
        change.index.undo = delta.transformPosition(change.index.undo, true);
        change.index.redo = delta.transformPosition(change.index.redo, true);
      }
    }
    for (let change of this.stack.redo) {
      if (change.index) {
        change.index.undo = delta.transformPosition(change.index.undo, true);
        change.index.redo = delta.transformPosition(change.index.redo, true);
      }
    }
  }

  undoOnBackspace() {
    this._undoOnBackspace = true;
  }
}

// Override
// Defer to the precomputed index, if present
function getLastChangeIndex(delta) {
  // Measure the length minus deletes
  let deleteLength = delta.reduce(function(length, op) {
    length += (op.delete || 0);
    return length;
  }, 0);
  let changeIndex = delta.length() - deleteLength;
  if (endsWithNewlineChange(delta)) {
    changeIndex -= 1;
  }
  return changeIndex;
}

// Copied here as a dependency of getLastChangeIndex
function endsWithNewlineChange(delta) {
  let lastOp = delta.ops[delta.ops.length - 1];
  if (lastOp == null) return false;
  if (lastOp.insert != null) {
    return typeof lastOp.insert === 'string' && lastOp.insert.endsWith('\n');
  }
  if (lastOp.attributes != null) {
    return Object.keys(lastOp.attributes).some(function(attr) {
      return Parchment.query(attr, Parchment.Scope.BLOCK) != null;
    });
  }
  return false;
}

Quill.register('modules/history', History, true);

export default History;
