var clone = require('lodash').clone;
var pick = require('lodash').pick;

module.exports = function allowlistFormats(delta, formats) {
  var ops = delta.ops.map(function(op) {
    op = clone(op);
    if (op.attributes) {
      op.attributes = pick(op.attributes, formats);
    }
    return op;
  });

  return { ops: ops };
};
