import Quill from 'quill';
import { Delta } from 'rich-text';

class DoubleSpaces {
  constructor(quill) {
    let ignore = false;

    quill.on('debounced-text-change', (delta, oldContents, source) => {
      if (ignore || source !== 'user') {
        return;
      }

      const text = quill.getContents().reduce((text, op) => {
        return text + (typeof op.insert === 'string' ? op.insert : '!');
      }, '');
      const fixDelta = new Delta();
      let index = 0;

      for (let op of delta.ops) {
        if (op.delete) {
          if (op.delete > 1) { // only take action on range deletions
            const before = text.charAt(index - 1);
            const after = text.charAt(index);
            if (before === ' ' && after === ' ') {
              fixDelta.delete(1);
              index += 1;
            }
          }
        } else {
          const length = op.retain || op.insert.length || 1;
          fixDelta.retain(length);
          index += length;
        }
      }

      if (fixDelta.chop().length() > 0) {
        ignore = true;
        quill.updateContents(fixDelta, 'user');
        ignore = false;
      }
    });
  }
}

Quill.register('modules/double-spaces', DoubleSpaces);

export default DoubleSpaces;
