import Quill from 'quill';
import { bindAll, get as getPath } from 'lodash';
import { Delta } from 'rich-text';
import isolateObjects from '../../isolate_objects';
import removeInvalidFormats from '../../remove_invalid_formats';
import removeIncompleteGroups from '../../remove_incomplete_groups';
import setLineDefaults from '../../set_line_defaults';
import iframeReady from '../../iframe_ready';

class ObjectBehavior {
  constructor(quill) {
    bindAll(this, 'onTextChange', 'onSelectionChange', 'onClick');
    this.quill = quill;
    this.selectedIframes = [];
    this.preventEdit = false;
    quill.on('debounced-text-change', this.onTextChange);
    quill.on('selection-change', this.onSelectionChange);
    quill.root.addEventListener('click', this.onClick);
  }

  destroy() {
    this.quill.root.removeEventListener('click', this.onClick);
  }

  onTextChange() {
    this.onSelectionChange();

    // Prevent recursion
    if (this.preventEdit) { return; }

    // Compose the various fix deltas
    var fixDelta = this.buildFixDelta();

    if (fixDelta.length() > 0) {
      this.preventEdit = true;
      this.quill.updateContents(fixDelta, 'user');
      this.preventEdit = false;
    }
  }

  buildFixDelta() {
    var contents = this.quill.getContents();
    var result = [
      isolateObjects,
      removeInvalidFormats,
      removeIncompleteGroups,
      setLineDefaults
    ].reduce((fixDelta, fixFunc) => {
      var currentContents = contents.compose(fixDelta);
      return fixDelta.compose(fixFunc(currentContents));
    }, new Delta());

    return result.chop();
  }

  // Check if the selection consists of only one object.
  // If so, call selectIframes(iframesInRange), else call selectIframes([]).
  onSelectionChange() {
    // Do not un-select any iframe if we lose focus
    // We want the iframe to appear selected even when a sidebar editing UI is open
    if (document.activeElement !== this.quill.root) {
      return;
    }

    var selection = document.getSelection();
    var range = selection.rangeCount > 0 && selection.getRangeAt(0);

    // If there is no selection range, bail
    if (!range) {
      return;
    }

    if (range.collapsed) {
      // common case, a collapsed selection cannot possibly contain iframes
      this.selectIframes([]);
    } else {
      this.selectIframes(findInRange(range, 'iframe'));
    }
  }

  onClick(e) {
    var enabled = this.quill.root.isContentEditable;
    if (enabled && e.target && e.target.tagName === 'IFRAME') {
      var selection = document.getSelection();
      var range = document.createRange();
      range.setStartBefore(e.target);
      range.setEndAfter(e.target);
      selection.removeAllRanges();
      selection.addRange(range);
      this.quill.root.focus();
    }
  }

  selectIframes(iframes) {
    // Deselection
    var selectedFrameIds = [];
    var frameIds = iframes.map(iframe => getPath(iframe, ['dataset', 'frameId']));

    for (let iframe of this.selectedIframes) {
      var frameId = getPath(iframe, ['dataset', 'frameId']);
      var body = getPath(iframe, ['contentWindow', 'document', 'body']);
      if (frameIds.includes(frameId)) {
        selectedFrameIds.push(frameId);
      } else {
        // If a selected iframe is removed, the body content within won't exist anymore
        if (body) {
          body.classList.remove('is-selected');
          body.classList.remove('is-active');
        }
        app.emit('object.deselected', frameId);
      }
    }

    // Selection
    if (iframes.length > 0) {
      for (let iframe of iframes) {
        iframeReady(iframe).then(() => {
          var frameId = iframe.dataset.frameId;
          if (!selectedFrameIds.includes(frameId)) {
            iframe.contentWindow.document.body.classList.add('is-selected');
            iframe.contentWindow.document.body.classList.toggle('is-active', iframes.length === 1);
            app.emit('object.selected', frameId);
            iframe.contentWindow.postMessage({
              event: 'object.selected',
              payload: frameId
            }, window.location.origin);
          }
        });
      }
    }

    this.selectedIframes = iframes;
  }
}

function findInRange(range, selector) {
  var container = range.commonAncestorContainer;

  if (container.nodeType !== Node.ELEMENT_NODE) {
    return [];
  }

  var nodeRange = document.createRange();
  return [...container.querySelectorAll(selector)].filter(node => {
    nodeRange.selectNode(node);
    var startsBefore = range.compareBoundaryPoints(Range.START_TO_START, nodeRange) <= 0;
    var endsAfter = range.compareBoundaryPoints(Range.END_TO_END, nodeRange) >= 0;
    return startsBefore && endsAfter;
  });
}

Quill.register('modules/object-behavior', ObjectBehavior);

export default ObjectBehavior;
