import Quill from 'quill';
import ObjectBlot from './object';

const USES_FIXED_HEIGHT = [
  'gallery',
  'imageslider',
  'ratingcard'
];

class Doc extends ObjectBlot {
  static create(value) {
    let node = super.create(value);
    node.classList.add(`${value.type}-object`);
    if (USES_FIXED_HEIGHT.includes(value.type)) {
      node.dataset.preserveHeight = '';
    }
    return node;
  }
}

Doc.blotName = 'doc';

Quill.register('formats/doc', Doc);

export default Doc;
