var autosave = require('../../../actions/autosave');

export default function() {
  const delta = [];
  const paths = [
    ['draft', 'custom_field_flags'],
    ['draft', 'custom_field_texts']
  ];

  for (const path of paths) {
    const wrapper = app.state.get(['doc', 'head', 'contents', ...path]);
    if (typeof wrapper !== 'object') {
      delta.push({
        p: path,
        oi: {}
      });
    }
  }

  if (delta.length > 0) {
    autosave.localUpdate(app.state.get(['doc', 'id']), delta, -1);
  }
}
