import Quill from 'quill';

const TEMPLATE = `
  <span class="cursor-flag">
    <span class="cursor-name"></span>
  </span>
  <span class="cursor-caret"></span>
`;

const DEFAULTS = {
  timeout: 2500
};

class MultiCursor {
  constructor(quill, options) {
    this.quill = quill;
    this.options = Object.assign({}, DEFAULTS, options);
    this.container = quill.addContainer('ql-multi-cursor');
    this.cursors = {};
    quill.on('text-change', this._applyDelta, this);
  }

  clearCursors() {
    for (let id of Object.keys(this.cursors)) {
      this.removeCursor(id);
    }
    this.cursors = {};
  }

  moveCursor(id, index) {
    const cursor = this.cursors[id];
    if (cursor == null) return;
    cursor.index = index;
    cursor.elem.classList.remove('hidden');
    clearTimeout(cursor.timer);
    cursor.timer = setTimeout(() => {
      cursor.elem.classList.add('hidden');
      cursor.timer = null;
    }, this.options.timeout);
    this._updateCursor(cursor);
    return cursor;
  }

  removeCursor(id) {
    const cursor = this.cursors[id];
    if (cursor) {
      cursor.elem.parentNode.removeChild(cursor.elem);
    }
    delete this.cursors[id];
  }

  setCursor(id, index, name, color) {
    if (this.cursors[id] == null) {
      this.cursors[id] = {
        id,
        index,
        color,
        elem: this._buildCursor(name, color)
      };
    }
    setTimeout(() => {
      this.moveCursor(id, index);
    }, 0);
    return this.cursors[id];
  }

  shiftCursors(index, length) {
    for (let cursor of Object.values(this.cursors)) {
      if (!cursor || cursor.index <= index) continue;
      const shift = Math.max(length, index - cursor.index);
      cursor.index += shift;
    }
  }

  update() {
    for (let cursor of Object.values(this.cursors)) {
      if (!cursor) continue;
      this._updateCursor(cursor);
    }
  }

  _applyDelta(delta) {
    let index = 0;
    for (let op of delta.ops) {
      let length = 0;
      if (op.insert) {
        this.shiftCursors(index, op.insert.length || 1);
      } else if (op.delete) {
        this.shiftCursors(index, -1 * op.delete);
      } else if (op.retain) {
        this.shiftCursors(index, 0);
        length = op.retain;
      }
      index += length;
    }
    this.update();
  }

  _buildCursor(name, color) {
    const cursor = document.createElement('span');
    cursor.classList.add('cursor');
    cursor.innerHTML = TEMPLATE;
    const cursorName = cursor.querySelector('.cursor-name');
    cursorName.textContent = name;
    const cursorCaret = cursor.querySelector('.cursor-caret');
    cursorCaret.style.backgroundColor = cursorName.style.backgroundColor = color;
    this.container.appendChild(cursor);
    return cursor;
  }

  _updateCursor(cursor) {
    const bounds = this.quill.getBounds(cursor.index);
    if (!bounds) return this.removeCursor(cursor.id);
    cursor.elem.style.top = (bounds.top + this.quill.container.scrollTop) + 'px';
    cursor.elem.style.left = bounds.left + 'px';
    cursor.elem.style.height = bounds.height + 'px';
    const flag = cursor.elem.querySelector('.cursor-flag');
    cursor.elem.classList.toggle('top', parseInt(cursor.elem.style.top, 10) <= flag.offsetHeight);
    cursor.elem.classList.toggle('left', parseInt(cursor.elem.style.left, 10) <= flag.offsetWidth);
    cursor.elem.classList.toggle('right', this.quill.root.offsetWidth - parseInt(cursor.elem.style.left, 10) <= flag.offsetWidth);
  }
}

Quill.register('modules/multi-cursor', MultiCursor);

export default MultiCursor;
