const { addPage, getCustomstoryStatus } = require('../../../actions/customstory');

const SHORT_WAIT = 5000;
const LONG_WAIT = 30000;

export default async function initCustomstories() {
  const pages = app.state.get(['doc', 'head', 'contents', 'draft', 'customstory_pages']);
  if (pages.length === 0) {
    await addPage(0);
  }

  app.state.set(['customstoryStatus'], {
    statusLabel: 'Working',
    statusMessage: '',
    statusPending: true
  });

  pollForBuildStatus();

  return Promise.resolve();
}

async function delay(interval) {
  await new Promise(resolve => {
    setTimeout(resolve, interval);
  });
}

async function pollForBuildStatus(interval = 0) {
  while (interval > 0) {
    let recentlySent = app.state.get('customstoryBuildSent');
    if (recentlySent) {
      interval = 1000;
      app.state.set('customstoryBuildSent', false);
    }
    await delay(1000);
    interval -= 1000;
  }
  getBuildStatus();
}

async function getBuildStatus() {
  const { statusLabel } = await getCustomstoryStatus();

  let nextInterval = LONG_WAIT;
  if (['Building', 'Build queued'].includes(statusLabel)) {
    nextInterval = SHORT_WAIT;
  }

  pollForBuildStatus(nextInterval);
}
