var contributorActions = require('../../../actions/contributors');

export default function initContributors() {
  var contributors = app.state.get(['doc', 'head', 'contents', 'draft', 'contributors']);

  // If there are no contributors, save the doc owner as the contributor
  if (contributors.length === 0) {
    var docUserId = app.state.get(['doc', 'user_id']);
    var userId = docUserId === -1 ? app.state.get(['user', 'id']) : docUserId;
    return contributorActions.addContributor(userId, 0, -1);
  }
}
