const { Delta } = require('rich-text');

// Insert newlines just before or after an object op if not already present
module.exports = function isolateObjects(contents) {
  let delta = new Delta();
  let objectWasLast = false;
  let beginningOfLine = true;

  for (let [i, op] of contents.ops.entries()) {
    // look ahead for the next op containing a `\n`
    const lineOp = contents.ops.slice(i).find(op => /\n/.test(op.insert));

    if (typeof op.insert === 'string') {
      // If an object was last, and the first character is not a newline,
      // add one to isolate the object on the right side.
      if (objectWasLast && op.insert.charAt(0) !== '\n') {
        delta.insert('\n', lineOp.attributes);
      }
      delta.retain(op.insert.length);
      objectWasLast = false;
      beginningOfLine = (op.insert.charAt(op.insert.length - 1) === '\n');
    } else {
      // If op.insert isn't a string, we are inserting an object into the doc body.
      // If this isn't the beginning of a line, insert a newline immediately before
      // the object to isolate the object on the left side.
      if (!beginningOfLine) {
        delta.insert('\n', lineOp.attributes);
      }
      delta.retain(1);
      objectWasLast = true;
      beginningOfLine = false;
    }
  }

  return delta.chop();
};
