import Quill from 'quill';
import LinkBlot from '../blots/link';
import isValidUrl from '../../is_valid_url';
import { INVALID_LINK_MESSAGE } from '../../../config/messages';

const TEMPLATE = `
  <div class="ql-link-tooltip__wrapper">
    <a class="url" target="_blank" rel="external"></a>
    <input type="text" class="input">
    <a class="change">Change</a>
    <a class="remove">Remove</a>
    <a class="done">Done</a>
  </div>
  <div class="ql-link-tooltip__warning"></div>
`;

const DEFAULTS = {
  maxLength: 50,
  horizontalPadding: 5,
  verticalPadding: 5
};

class LinkTooltip {
  constructor(quill, options) {
    this.quill = quill;
    this.options = Object.assign({}, DEFAULTS, options);
    this.root = quill.addContainer('ql-link-tooltip');
    this.root.innerHTML = TEMPLATE;
    this.textbox = this.root.querySelector('input');
    this.preview = this.root.querySelector('a.url');
    this.warning = this.root.querySelector('.ql-link-tooltip__warning');

    // Save or cancel, on certain keypresses
    this.textbox.addEventListener('keydown', (event) => {
      if (event.key === 'Enter') {
        this.save();
        event.preventDefault();
      } else if (event.key === 'Escape') {
        this.cancel();
        event.preventDefault();
        event.stopPropagation();
      }
    });

    // Clicking the change button toggles on edit mode
    this.root.querySelector('a.change').addEventListener('click', (event) => {
      this.edit(this.preview.textContent);
      event.preventDefault();
    });

    // Clicking the done button saves the link
    this.root.querySelector('a.done').addEventListener('click', (event) => {
      this.save();
      event.preventDefault();
    });

    // Clicking the remove button removes the link from the saved range
    this.root.querySelector('a.remove').addEventListener('click', (event) => {
      this.toggle(false);
      event.preventDefault();
    });

    this.quill.on('selection-change', (...args) => {
      // Ensure that quill.scrollingContainer has had it's scrollTop restored
      // by quill.focus() before positioning
      Promise.resolve().then(() => this.onSelectionChange(...args));
    });

    this.hide();
  }

  cancel() {
    this.quill.setSelection(this.quill.selection.savedRange, 'user');
  }

  edit(url) {
    this.root.classList.remove('hidden');
    this.root.classList.add('editing');
    this.textbox.value = url;
    this.position(this.quill.getBounds(this.quill.selection.savedRange));
    this.textbox.select();
  }

  format(value) {
    let range = this.quill.selection.savedRange;
    if (range.length === 0 && this.linkRange) {
      range = this.linkRange;
    }
    this.quill.formatText(range, 'link', value, 'user');
  }

  hide() {
    if (!this.root.classList.contains('hidden')) {
      this.root.removeAttribute('style');
      this.root.classList.add('hidden');
      this.setWarning(null);
    }
  }

  normalize(url) {
    if (!/^(https?:\/\/|mailto:)/.test(url)) {
      url = `http://${url}`;
    }
    return url;
  }

  // Show the link tooltip whenever the selection changes to a range that
  // is encompassed by a single link/autolink blot
  onSelectionChange(range) {
    if (range == null) return;
    let [link, offset] = this.quill.scroll.descendant(LinkBlot, range.index);
    if (link != null && offset + range.length <= link.length()) {
      let value = LinkBlot.formats(link.domNode);
      this.preview.textContent = value;
      this.preview.href = value;
      this.linkRange = { index: range.index - offset, length: link.length() };
      this.show();
    } else {
      this.hide();
    }
  }

  position(reference) {
    // do nothing if element has not been appended to the DOM
    if (!document.body.contains(this.root)) {
      return;
    }
    let left = reference.left + reference.width/2 - this.root.offsetWidth/2;
    // since reference.bottom is relative to quill.container, we need to add in
    // quill.container.scrollTop in case quill.container is a scroll container,
    // i.e. aggrandized mode.
    let top = reference.bottom + this.quill.container.scrollTop + this.options.verticalPadding;
    this.root.style.left = left + 'px';
    this.root.style.top = top + 'px';
    this.root.classList.remove('ql-flip');
    let containerBounds = this.root.offsetParent.getBoundingClientRect();
    let rootBounds = this.root.getBoundingClientRect();
    if (containerBounds.right - rootBounds.right < this.options.horizontalPadding) {
      let shift = containerBounds.right - rootBounds.right - (2 * this.options.horizontalPadding);
      this.root.style.left = (left + shift) + 'px';
    }
    if (rootBounds.left - containerBounds.left < this.options.horizontalPadding) {
      let shift = containerBounds.left - rootBounds.left + this.options.horizontalPadding;
      this.root.style.left = (left + shift) + 'px';
    }
    if (rootBounds.bottom > document.documentElement.clientHeight - this.options.verticalPadding) {
      let shift = reference.height + rootBounds.height + 2 * this.options.verticalPadding;
      this.root.style.top = (top - shift) + 'px';
      this.root.classList.add('ql-flip');
    }
  }

  reposition() {
    if (!this.root.classList.contains('hidden')) {
      this.position(this.quill.getBounds(this.quill.selection.savedRange));
    }
  }

  save() {
    let value = this.textbox.value;

    if (!isValidUrl(value)) {
      this.setWarning(INVALID_LINK_MESSAGE);
      return;
    } else {
      this.setWarning(null);
    }

    value = this.normalize(value);
    this.format(value);
    this.quill.focus();
  }

  setWarning(text) {
    this.warning.textContent = text;
    this.warning.classList.toggle('is-active', text != null);
  }

  show() {
    this.root.classList.remove('editing');
    this.root.classList.remove('hidden');
    this.position(this.quill.getBounds(this.linkRange));
  }

  toggle(value) {
    if (value) {
      let range = this.quill.getSelection();
      if (range == null || range.length === 0) return;
      let url = this.quill.getText(range);
      if (/^\S+@\S+\.\S+$/.test(url) && url.indexOf('mailto:') !== 0) {
        url = 'mailto:' + url;
      }
      this.edit(url);
    } else {
      this.format(value);
      this.quill.focus();
      this.hide();
    }
  }
}

Quill.register('modules/link-tooltip', LinkTooltip);

export default LinkTooltip;
