var { memberAdded, memberRemoved, setStreamMembers } = require('../../../actions/streams');

export default function initStreamMembers() {
  // Receive stream updates from the server and apply locally
  app.contentApiSocket.on('stream.story-added', memberAdded);
  app.contentApiSocket.on('stream.story-removed', memberRemoved);

  app.once('autosave.error', () => {
    // Stop receiving remote stream updates if error occurs
    app.contentApiSocket.off('stream.story-added', memberAdded);
    app.contentApiSocket.off('stream.story-removed', memberRemoved);
  });

  setStreamMembers();
}
