import Quill from 'quill';
import BooleanDataAttributor from '../attributors/boolean_data_attributor';

const Parchment = Quill.import('parchment');
const Dropcap = new BooleanDataAttributor('dropcap', {
  scope: Parchment.Scope.BLOCK
});

Quill.register('formats/dropcap', Dropcap);

export default Dropcap;
