// Normally, when lines are merged due to a deletion, the line from before the
// deletion controls the resulting formatting of the merged line.
//
// When it comes to the "br" format, we want to use the opposite heuristic:
// it's the line from after the deletion that should control the resulting
// formatting of the merged line.
//
// TODO: remove soft break preceeding a header or block quote

import Quill from 'quill';

class SoftBreaks {
  constructor(quill) {
    quill.on('debounced-text-change', (delta, oldContents, source) => {
      // Short circuit, if nothing was deleted, there's nothing to do
      if (source !== 'user' || !delta.ops.some(op => op.delete)) {
        return;
      }

      let index = 0;

      for (let op of delta.ops) {
        if (!op.delete) {
          index += op.retain || 0;
          continue;
        }

        let deletedContents = oldContents.slice(index, index + op.delete);

        // If the deleted contents did not contain a newline, there's nothing to do
        if (!deletedContents.ops.some((op) => /\n/.test(op.insert))) {
          continue;
        }

        let preLineFormats = findLineFormats(oldContents, index);
        let postLineFormats = findLineFormats(oldContents, index + op.delete);

        // If the "br" formats from before and after the deletion don't match,
        // then ensure that the value of the "br" format from after the deletion
        // is the one that is used.
        if (preLineFormats.br !== postLineFormats.br) {
          quill.formatLine(index, 1, { br: postLineFormats.br }, 'user');
        }
      }
    });
  }
}

function findLineFormats(contents, index) {
  for (let op of contents.slice(index).ops) {
    if (typeof op.insert === 'string' && op.insert.includes('\n')) {
      return op.attributes || {};
    }
  }
  return {};
}

Quill.register('modules/soft-breaks', SoftBreaks);

export default SoftBreaks;
