const moment = require('moment-timezone');
const { orderBy, property } = require('lodash');
const tokenizer = require('sbd');
const commaJoin = require('../lib/comma_join');
const urlForEntry = require('../lib/url_for_entry');
const isPublished = require('../lib/is_published');
const queryRegex = require('../../shared/query_regex');
const messageActions = require('./messages');
const docActions = require('./docs');
const autosave = require('./autosave');
const { toPlaintext } = require('convert-rich-text');
const sentenceCase = require('../../shared/sentence_case');
const getEventDateTime = require('../../shared/get_event_datetime');
const isStream = require('../../shared/is_stream');
const isLinkPost = require('../../shared/is_link_post');
const { INVALID_STREAM_MEMBERSHIP } = require('../config/messages');

/**
 * Search for streams in Content API; filter out members that are
 * already present in state.
 * @return {Object} results An array of stream hashes with data for UI
 */
export async function searchStreams(q, streams) {
  let query = {
    q: q + ' type:stream',
    community_id: app.state.get(['community', 'id']),
    schema_name: 'story',
    per_page: 5,
    archived: false,
    trashed: false
  };

  let { docs } = await docActions.searchDocs(query);
  let streamIds = new Set(streams.map(stream => stream.doc_id));

  // filter out streams that already exist in app state
  docs = docs.filter(doc => !streamIds.has(doc.id));

  let results = docs.map(doc => {
    return {
      text: doc.title,
      value: {
        doc_id: doc.id,
        title: doc.title
      }
    };
  });

  if (results.length === 0) {
    return [{ value: null, text: 'No streams found' }];
  }

  return results;
}

/**
 * Search for members in Content API; filter out members that are
 * already present in state or are of type Stream or LinkPost.
 * @return {Object} results An array of member hashes containing formatted data
 *                  for UI.
 */
export async function searchMembers(q) {
  let query = {
    q,
    community_id: app.state.get(['community', 'id']),
    schema_name: 'story',
    order: 'date',
    story_type_blocklist: ['stream', 'link'],
    archived: false,
    trashed: false
  };

  let { docs } = await docActions.searchDocs(query);
  let memberIds = new Set(app.state.get(['stream', 'docs']).map(doc => doc.id));

  // exact match is found, but it already exists in stream
  if (docs.length === 1 && memberIds.has(docs[0].id)) {
    return [{ value: null, text: 'Story already in stream' }];
  }

  // filter out members that already exist in app state
  docs = docs.filter(doc => !memberIds.has(doc.id));

  let results = docs.map(doc => {
    return formatStreamMember(doc, q);
  });

  if (results.length === 0) {
    return [{ value: null, text: 'No results found' }];
  }

  return results;
}

/**
 * Searches for a member by its URL; returns matched doc if found or invalid
 * message if search result returned a doc of type Stream or LinkPost.
 * @param {String} url A URL used to query for docs
 *                NOTE: `http[s]://` protocol will be removed as public_url is
 *                not stored with protocols in the content-api ES index
 * @return {Object} results An array member hashes containing of formatted data
 *                  for UI.
 */
export async function getMemberByUrl(url) {
  const query = {
    q: url,
    community_id: app.state.get(['community', 'id']),
    schema_name: 'story',
    archived: false,
    trashed: false
  };
  const { docs } = await docActions.searchDocs(query);
  const memberIds = new Set(app.state.get(['stream', 'docs']).map(doc => doc.id));
  const doc = docs[0];

  // exact match is found, but it already exists in stream
  if (doc && memberIds.has(doc.id)) {
    return [{ value: null, text: 'Story already in stream' }];
  } else if (!doc) {
    return [{ value: null, text: 'No results found' }];
  } else if (isStream(doc.head.contents) || isLinkPost(doc.head.contents)) {
    return [{ value: null, text: INVALID_STREAM_MEMBERSHIP }];
  } else {
    return [formatStreamMember(doc)];
  }
}

/**
 * Gets a list of stream docs on a member; returns simplified hash of stream
 * details for use in autocomplete add/remove UI
 * @return {Promise} promise of an streams array containing hashes of stream
 *                   data including the streams ID and formatted title
 */
export async function getStreamsForMember() {
  let streams = app.state.get(['doc', 'head', 'contents', 'streams']);

  if(!streams || streams.length === 0) { return []; }

  let streamIds = streams.map(stream => stream.id).join(',');
  const { docs } = await docActions.searchDocs({ ids: streamIds });

  return docs.map(doc => {
    return {
      doc_id: doc.id,
      title: doc.title
    };
  });
}

/**
 * Stores the visible set of stream members in app state memory, making sure
 * to preserve reverse-chron order when inserting and deleting members.
 */
export async function setStreamMembers(page = 1) {
  if (app.state.get(['docLabel']) !== 'stream') { return; }

  const { docs, pagination } = await docActions.searchDocs({
    schema_name: 'story',
    stream_id: app.state.get(['doc', 'id']),
    order: 'date',
    page,
  });

  app.state.set(['stream', 'docs'], docs);
  app.state.set(['stream', 'pagination'], pagination);
}

/**
 * Adds stream ID to a member
 * @param {Number} streamDocId A valid doc ID for a stream
 * @param {Number} memberDocId A valid doc ID for a member adding streamId
 * @return {Promise} promise returned of member doc update action
 */
export async function addStreamMember(streamDocId, memberDocId) {
  // If called from a member
  if (app.state.get(['doc', 'id']) === memberDocId) {
    let streams = app.state.get(['doc', 'head', 'contents', 'streams']);
    let delta = [];

    if (streams) {
      delta.push({ p: ['streams', streams.length], li: { id: streamDocId } });
    } else {
      delta.push({ p: ['streams'], oi: [{ id: streamDocId }] });
    }

    autosave.localUpdate(memberDocId, delta);
  } else { // If called from a stream
    const doc = await docActions.getDoc(memberDocId);
    let streams = doc.head.contents.streams;
    let delta = [];

    if (streams) {
      delta.push({ p: ['streams', streams.length], li: { id: streamDocId } });
    } else {
      delta.push({ p: ['streams'], oi: [{ id: streamDocId }] });
    }

    // Because docs being added to streams are not kept in app state, we will
    // need to use the API to update the doc
    docActions.updateDoc(memberDocId, { delta });
  }
}

/**
 * Removes stream ID from a member
 * @param {Number} streamDocId A valid doc ID for a stream
 * @param {Number} memberDocId A valid doc ID for a member removing streamId
 * @return {Promise} A promise returned of member doc update action
 */
export async function removeStreamMember(streamDocId, memberDocId) {
  // If called from a member
  if (app.state.get(['doc', 'id']) === memberDocId) {
    let streams = app.state.get(['doc', 'head', 'contents', 'streams']);

    if (streams) {
      let streamDocIndex = streams.findIndex(stream => stream.id === streamDocId);

      if (streamDocIndex !== -1) {
        autosave.localUpdate(memberDocId, [{
          p: ['streams', streamDocIndex],
          ld: { id: streamDocId }
        }]);
      }
    }
  } else { // If called from a stream
    const doc = await docActions.getDoc(memberDocId);
    let streams = doc.head.contents.streams;

    if (streams) {
      let streamDocIndex = streams.findIndex(stream => stream.id === streamDocId);

      if (streamDocIndex !== -1) {
        // Because docs being added to streams are not kept in app state, we will
        // need to use the API to update the doc
        docActions.updateDoc(
          memberDocId,
          {
            delta: [{ p: ['streams', streamDocIndex], ld: { id: streamDocId } }]
          }
        );
      }
    }

  }
}

/**
 * Callback method for added stream to a member event attached to the listener
 * in client/initializers/editor/doc/stream_members.js. Displays message if
 * stream and member are both published.
 * @param {Object} doc JSON doc of member added to stream
 */
export function memberAdded(doc) {
  // clone the value to bust two-way binding
  let streamMembers = app.state.get(['stream', 'docs']).slice();
  streamMembers.push(doc);
  // to match the streams API, sort by updated at date descending, title ascending (for tiebreakers)
  streamMembers = orderBy(streamMembers, [property('doc.updated_at'), property('doc.title')], ['desc', 'asc']);
  app.state.set(['stream', 'docs'], streamMembers);

  // Reflect updated total for paginated streams
  const totalMembers = app.state.get(['stream', 'pagination', 'total']) || 0;
  app.state.set(['stream', 'pagination', 'total'], totalMembers + 1);

  showDocMessage('added', doc);
}

/**
 * Callback method for removed stream from a member event attached to the
 * listener in client/initializers/editor/doc/stream_members.js
 * @param {Object} doc JSON doc of member removed from stream
 */
export function memberRemoved(doc) {
  // clone the value to bust two-way binding
  let streamMembers = app.state.get(['stream', 'docs']).slice();
  let newStreamMembers = streamMembers.filter(memberDoc => memberDoc.id !== doc.id);

  app.state.set(['stream', 'docs'], newStreamMembers);

  // Reflect updated total for paginated streams
  const totalMembers = app.state.get(['stream', 'pagination', 'total']) || 0;
  totalMembers > 0 && app.state.set(['stream', 'pagination', 'total'], totalMembers - 1);

  showDocMessage('removed', doc);
}

/**
 * Formats stream member results; highlights any matched text from the query
 * @param {Object} doc JSON doc of member result from search
 * @param {String} query query string from search
 * @return {Object} A hash of formatted member results
 */
function formatStreamMember(doc, q = '') {
  // case-insensitive regex for search terms
  let regex = queryRegex(q);

  // 1. Title: highlight search terms
  let title = q === '' ? doc.title : highlightKeywords(doc.title, regex);
  title = `<span class='p-story__result__title'>${title}</span>`;

  // 2. Body: sanitize html and find the first sentence with highlighted search terms,
  // otherwise include first sentence
  let sentences = tokenizer.sentences(toPlaintext(doc.head.contents.draft.body), { sanitize: true });
  let keywordSentence = sentences.find(sentence => regex.test(sentence));
  let body = keywordSentence ? highlightKeywords(keywordSentence, regex) : sentences[0];
  if (body) {
    body = `<p class='p-story__result__dek'>${body}</p>`;
  }

  // 3. Metadata: format byline, current status and modified date
  let byline = commaJoin(doc.contributors.map(contributor => `<span class='p-story__result__byline'>${contributor.fullOrUserName}</span>`));
  let status = doc.head.contents.hidden ? 'Hidden' : sentenceCase(doc.status.toLowerCase());
  let date = getStatusDate(doc);
  let metadata = `
    <span class='p-story__result__status ${status.toLowerCase()}'>${status}</span>
    ${isPublished(doc.status) ? `by ${byline}` : ''}
    ${status === 'Draft' ? 'modified on' : 'on'}
    <span class='p-story__result__date'>${date}</span>
  `;
  let additionalText = body ? body + metadata : metadata;

  return {
    text: title,
    additional_text: additionalText,
    value: {
      doc_id: doc.id
    }
  };
}

/**
 * Takes a string of text and RegEx query and returns HTML string with matched
 * text wrapped in a tag to highlight it.
 * @param {String} text A string of text to be highlighted
 * @param {RegExp} regex A regex pattern for matching submitted text
 * @return {String} A HTML string containing formatted text with highlight markup
 */
function highlightKeywords(text, regex) {
  return text.replace(regex, (match) => {
    return `<span class='p-story__result__highlight'>${match}</span>`;
  });
}

/**
 * Display a flash message for when a published member is linked to a published
 * stream.
 * @param {String} action A string for doc event. Only ['added', 'removed']
 * @param {Object} doc A JSON doc of member
 */
function showDocMessage(action, doc) {
  let streamDoc = app.state.get('doc');

  // display message only if stream and member are both published
  if (isPublished(streamDoc.status) && isPublished(doc.status)) {
    let streamUrl = urlForEntry(streamDoc.entry_id);
    let docUrl = `/compose/${doc.uuid}`;
    let link = (url, text) => {
      return `<a href="${url}" target="_blank" rel="nofollow noopener">${text}</a>`;
    };
    let message = `A story was ${action}. ${link(docUrl, 'Edit story')} ${link(streamUrl, 'View stream')}.`;

    messageActions.showMessage({ type: 'positive', message });
  }
}

/**
 * Gets status date for a doc. If the doc is published|updated, it will return
 * historical publish date otherwise it returns updated_at dates.
 * @param {Object} doc JSON doc to get formatted status date from
 * @return {String} formatted display date for doc
 */
function getStatusDate(doc) {
  let time = isPublished(doc.status)
    ? getEventDateTime(doc.head.contents.publish_history, ['published', 'published_via_schedule', 'backdated'])
    : doc.updated_at;

  return moment(time).format(app.config.dateFormat);
}
