// stolen from https://www.npmjs.com/package/keyword-highlighter
module.exports = function queryRegex(q) {
  var regex = q.split(' ');
  regex = regex.filter((char) => {
    return char !== '';
  });
  regex = regex.join('|');
  regex = regex.replace(/[-[\]{}()*+?.,\\^$]/g, '\\$&');
  return new RegExp(regex, 'gi');
};
