import Quill from 'quill';
import { isEqual } from 'lodash';

const Inline = Quill.import('blots/inline');
const PROTOCOLS = ['http:', 'https:', 'mailto:', 'tel:'];

function sanitize(url) {
  try {
    return PROTOCOLS.includes(new URL(url).protocol) ? url : 'about:blank';
  } catch (err) {
    return 'about:blank';
  }
}

class Autolink extends Inline {
  static create(value) {
    let node = super.create(value);
    node.dataset.autolinkId = value.id;
    node.dataset.autolinkHref = sanitize(value.href);
    if (value.disabled) {
      node.dataset.autolinkDisabled = '';
    }
    return node;
  }

  static formats(domNode) {
    return {
      id: domNode.dataset.autolinkId,
      href: domNode.dataset.autolinkHref,
      disabled: domNode.hasAttribute('data-autolink-disabled')
    };
  }

  // Override to perform deep equality check on formats to determine if we
  // should be merged with the next sibling
  optimize(context) {
    super.optimize(context);
    let next = this.next;
    if (next instanceof Autolink && next.prev === this && isEqual(this.formats(), next.formats())) {
      next.moveChildren(this);
      next.remove();
    }
  }
}

Inline.order.splice(Inline.order.indexOf('link'), 0, 'autolink');
Autolink.blotName = 'autolink';
Autolink.tagName = 'A';
Autolink.className = 'ql-autolink';

Quill.register('formats/autolink', Autolink);

export default Autolink;
