import Quill from 'quill';
import microtaskDebounce from '../../microtask_debounce';

class DebouncedTextChange {
  constructor(quill) {
    const reset = () => {
      this.delta = null;
      this.oldContents = null;
      this.source = null;
    };

    const emitNow = () => {
      if (this.delta && this.oldContents && this.source) {
        quill.emitter.emit('debounced-text-change', this.delta, this.oldContents, this.source);
      }
      reset();
    };

    const emitLater = microtaskDebounce(emitNow);

    quill.on('text-change', (delta, oldContents, source) => {
      if (this.source && source !== this.source) {
        emitNow();
      }
      if (!this.oldContents) {
        this.oldContents = oldContents;
      }
      if (this.delta) {
        this.delta = this.delta.compose(delta);
      } else {
        this.delta = delta;
      }
      this.source = source;
      emitLater();
    });
  }
}

Quill.register('modules/debounced-text-change', DebouncedTextChange);

export default DebouncedTextChange;
