import Quill from 'quill';
import { Delta } from 'rich-text';

const ID_CHARS = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
const ID_LENGTH = 6;

class LineIds {
  constructor(quill) {
    this.quill = quill;
    this.ignoreChange = false;

    // auto-add ids on every change
    this.quill.on('debounced-text-change', (delta, oldContents, source) => {
      if (!this.ignoreChange && source === 'user') {
        this.insertIds();
      }
    });
  }

  insertIds() {
    const contents = this.quill.getContents();
    const delta = new Delta();
    const usedIds = new Set();

    for (let op of contents.ops) {
      if (typeof op.insert !== 'string') {
        delta.retain(1);
      } else {
        let insert = op.insert;
        let offset = insert.indexOf('\n');
        while (offset >= 0) {
          delta.retain(offset);

          if (op.attributes != null && op.attributes.id != null && !usedIds.has(op.attributes.id)) {
            usedIds.add(op.attributes.id);
            delta.retain(1);
          } else {
            delta.retain(1, { id: this.makeId(usedIds) });
          }

          insert = insert.slice(offset + 1);
          offset = insert.indexOf('\n');
        }
        delta.retain(insert.length);
      }
    }

    if (delta.chop().length() > 0) {
      this.ignoreChange = true;
      this.quill.updateContents(delta, 'user');
      this.ignoreChange = false;
    }
  }

  // Make an id of `ID_LENGTH` random characters, ensuring it is not a member
  // of the `usedIds` set. Adds the id to the `usedIds` set before returning.
  makeId(usedIds) {
    let id;
    do {
      id = '';
      // eslint-disable-next-line no-restricted-syntax
      for (let i = 0; i < ID_LENGTH; i++) {
        id += ID_CHARS.charAt(Math.floor(Math.random() * ID_CHARS.length));
      }
    } while (usedIds.has(id));
    usedIds.add(id);
    return id;
  }
}

Quill.register('modules/line-ids', LineIds);

export default LineIds;
