var { Delta } = require('rich-text');
var hash = require('../../shared/content-hash');
var sentryActions = require('../actions/sentry');
var autosave = require('../actions/autosave');

var observers = {};

export function stop(docId, path) {
  // Clear any active intervals or timeouts for the current doc on load
  var intervalKey = stateKey(docId, path);
  if (observers[intervalKey]) {
    clearInterval(observers[intervalKey]);
    observers[intervalKey] = null;
  }
  var timeoutKey = errorKey(docId, path);
  if (observers[timeoutKey]) {
    clearTimeout(observers[timeoutKey]);
    observers[timeoutKey] = null;
  }
}

export function stopAll() {
  for (let key of Object.keys(observers)) {
    clearInterval(observers[key]);
    observers[key] = null;
  }
}

export function start(quill, docId, path) {
  stop(docId, path);

  // Setup the observer interval
  var interval = setInterval(() => checkState(quill, docId, path), app.config.stateObserverInterval);
  observers[stateKey(docId, path)] = interval;
}

function checkState(quill, docId, path, fire) {
  var autosaveError = app.state.get('autosave', 'error');

  // Only compare if not currently in the process of saving to client state &&
  // not already in an error state
  if (!autosave.isDirty(docId) && !autosaveError) {
    // Data in `app.state` untouched by Quill are vanilla Objects; Quill contents
    // are Deltas. The two cannot be compared via Lodash's isEqual. Using
    // Object.assign() assures all are vanilla Objects for comparing.
    var state = autosave.getDocCursor(docId).get(['head', 'contents'].concat(path));
    var contents = quill.getContents();
    var diff = new Delta(state).diff(contents);

    // Only fire an event if this is an unresolved, recurring mismatch
    if (diff.length() > 0) {
      if (fire) {
        var error = new Error(`Unexpected state mismatch at path: ${path}`);
        error.state = state;
        error.quill = contents;
        error.diff = diff;
        app.emit('autosave.error', error);
        sentryActions.reportError(error, false);
      } else {
        var timeout = setTimeout(() => checkState(quill, docId, path, true), 10000);
        observers[errorKey(docId, path)] = timeout;
      }
    }
  }
}

function stateKey(docId, path) {
  return hash(docId + path);
}

function errorKey(docId, path) {
  return hash(docId + path + 'error');
}
