import Quill from 'quill';

class Toolbar {
  constructor(quill, options) {
    this.quill = quill;
    if (typeof options.container === 'string') {
      this.container = document.querySelector(options.container);
    } else {
      this.container = options.container;
    }
    if (!(this.container instanceof HTMLElement)) {
      console.error('Container required for toolbar', options);
      return;
    }
    this.quill.on('editor-change', (type, range) => {
      if (type === 'selection-change') {
        this.update(range);
      }
    });
    this.quill.on('scroll-optimize', () => {
      let [range] = this.quill.selection.getRange();
      this.update(range);
    });
  }

  update(range) {
    let formats = range == null ? {} : this.quill.getFormat(range);
    let controls = this.container.querySelectorAll('button');
    for (let button of controls) {
      let format = Array.from(button.classList).find(c => c.indexOf('ql-') === 0);
      if (!format) continue;
      format = format.slice('ql-'.length);

      if (range == null) {
        button.classList.remove('ql-active');
      } else {
        button.classList.toggle('ql-active', formats[format] != null);
      }
    }
  }
}

Quill.register('modules/toolbar', Toolbar);

export default Toolbar;
