import Quill from 'quill';

const Inline = Quill.import('blots/inline');

class Bold extends Inline {
  // Use a simple boolean, despite tagName being an array,
  // which by default causes this to return: { bold: 'STRONG' }
  static formats() {
    return true;
  }

  // Ensure that the <strong> tag is used, not <b>
  optimize(context) {
    super.optimize(context);
    if (this.domNode.tagName !== 'STRONG') {
      this.replaceWith('bold');
    }
  }
}

Bold.blotName = 'bold';

// Support parsing both <strong> and <b> from pasted HTML
Bold.tagName = ['STRONG', 'B'];

Quill.register('formats/bold', Bold);

export default Bold;
