import Quill from 'quill';

const Inline = Quill.import('blots/inline');
const PROTOCOLS = ['http:', 'https:', 'mailto:', 'tel:'];
const BLANK = 'about:blank';

function sanitize(url) {
  try {
    return PROTOCOLS.includes(new URL(url).protocol) ? url : BLANK;
  } catch (err) {
    return BLANK;
  }
}

class Link extends Inline {
  static create(value) {
    const sanitizedValue = sanitize(value);
    const hasLink = sanitizedValue !== BLANK;

    let node = super.create(sanitizedValue);
    if (hasLink) {
      node.setAttribute('href', sanitizedValue);
      node.setAttribute('target', '_blank');
    }
    return node;
  }

  static formats(domNode) {
    return domNode.getAttribute('href');
  }

  format(name, value) {
    const sanitizedValue = sanitize(value);
    if (name !== this.statics.blotName || !value || sanitizedValue === BLANK) {
      return super.format(name, value);
    }
    this.domNode.setAttribute('href', sanitizedValue);
  }
}

Link.blotName = 'link';
Link.tagName = 'A';
Link.className = 'ql-link';

Quill.register('formats/link', Link);

export default Link;
