var autosave = require('../../../actions/autosave');
var { loadLinkSets } = require('../../../actions/link_sets');
var { isCommunityPostAuthor } = require('../../../lib/authorization');

export default function() {
  var linkSetId = app.state.get(['doc', 'head', 'contents', 'draft', 'link_set_id']);
  var docStatus = app.state.get(['doc', 'status']);
  var docId = app.state.get(['doc', 'id']);

  loadLinkSets().then(linkSets => {
    if (!linkSetId && docStatus === 'DRAFT') {
      var defaultLinkSet;
      if (isCommunityPostAuthor()) {
        defaultLinkSet = linkSets.find(l => l.name === 'Community');
      } else {
        defaultLinkSet = linkSets.find(l => l.default);
      }

      if (defaultLinkSet) {
        autosave.localUpdate(docId, [{ p: ['draft', 'link_set_id'], oi: defaultLinkSet.id }], -1);
      }
    } else if (linkSetId && !linkSets.find(l => l.id === linkSetId)) {
      // if the link set we have saved doesn't exist, delete it
      autosave.localUpdate(docId, [{ p: ['draft', 'link_set_id'], od: linkSetId, oi: null }], -1);
    }
  });
}
