const { SearchContributors } = require('../graphql/queries/users');
const userActions = require('./users');
const isPublished = require('../lib/is_published');
const graphqlRequest = require('../lib/graphql_request');
const autosave = require('./autosave');
const isPackage = require('../../shared/is_package');

export async function getContributors(text) {
  let contributorIds = app.state.get(['doc', 'head', 'contents', 'draft', 'contributors']).map(c => c.chorus_id);

  let { searchUsers } = await graphqlRequest('/api/tower/graphql', {
    query: SearchContributors,
    variables: { text, communityId: app.state.get(['doc', 'community_id']) }
  });
  // filter out users who are already saved as contributors
  return searchUsers.results.filter(user => !contributorIds.includes(user.id));
}

// the userId argument is used by the contributors initializer
export function addContributor(contributorId, index, userId = null) {
  // If the first user changes, update the story's Twitter data
  if (index === 0) {
    updateAutotweet(contributorId);
  }
  autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'contributors', index], li: { chorus_id: contributorId } }], userId);
}

export function removeContributor(contributorId, index) {
  // If the first user changes, update the story's Twitter data to the next user in the array
  if (index === 0) {
    updateAutotweet(app.state.get(['doc', 'head', 'contents', 'draft', 'contributors', 1, 'chorus_id']));
  }
  autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'contributors', index], ld: { chorus_id: contributorId } }]);
}

async function updateAutotweet(contributorId) {
  if (isPublished(app.state.get(['doc', 'status']))) {
    return;
  }

  let delta = [];
  const currentUserHandle = app.state.get(['doc', 'head', 'contents', 'draft', 'autotweets', 1, 'handle']) || null;
  const newUserHandle = contributorId ? await userActions.getTwitterUsername(contributorId) : null;

  if (newUserHandle && newUserHandle !== currentUserHandle) {
    // If present, update the user twitter handle when the author changes
    delta.push({
      p: ['draft', 'autotweets', 1, 'handle'],
      od: currentUserHandle,
      oi: newUserHandle
    });
  } else if (currentUserHandle) {
    // If there is no new user, just remove the current handle
    delta.push({
      p: ['draft', 'autotweets', 1, 'handle'],
      od: currentUserHandle
    });
  }

  if (delta.length > 0) {
    autosave.localUpdate(app.state.get(['doc', 'id']), delta, -1);
  }
}

export function contributorsForDoc(doc) {
  // When we reset the list of docs for the dashboard component after searching or
  // toggling the archive view, Ractive will invoke this function for every previous
  // doc with an `undefined` value. Since Ractive doesn't, we should guard against that.
  if (!doc) {
    return '';
  }

  if (isPackage(doc.head.contents)) {
    return `${app.state.get(['community', 'network', 'name'])} staff`;
  }

  let contributors = doc.contributors || [];
  if (contributors.length === 0) {
    return doc.owner.fullName;
  }

  // We only want to display the names of the first two contributors. If there are
  // any extra, display `+N` where N is the number of extra contributors.
  let head = contributors.slice(0, 2);
  let tail = contributors.slice(2);
  let result = head.map(c => c.fullOrUserName).join(', ');
  if (tail.length > 0) {
    result = `${result} +${tail.length}`;
  }

  return result;
}
