var autosave = require('../../../actions/autosave');

export default function() {
  const slugIsEvergreen = app.state.get(['doc', 'head', 'contents', 'draft', 'slug_is_evergreen']);
  if (typeof slugIsEvergreen === 'boolean') {
    return;
  }

  const entrySlugIsEvergreenDefault = app.state.get(['community', 'entrySlugIsEvergreenDefault']);
  if (typeof entrySlugIsEvergreenDefault !== 'boolean') {
    return;
  }

  autosave.localUpdate(app.state.get(['doc', 'id']), [{
    p: ['draft', 'slug_is_evergreen'],
    oi: entrySlugIsEvergreenDefault
  }], -1);
}
