var { sortBy, pick, get: getPath } = require('lodash');
var request = require('../lib/request');
var groupByFirstChar = require('../lib/group_by_first_char');
var autosave = require('./autosave');

const ENTRY_GROUP_ATTRIBUTES = [
  'id',
  'name',
  'slug',
  'parent_entry_group_id',
  'auto_add_subgroup_entries',
  'is_disclaimer'
];

export function loadGroups(type, communityId) {
  return request('/api/chorus/' + type + 's', {
    query: {
      per_page: 10000,
      community_id: communityId || app.state.get('doc', 'community_id'),
    },
    opts: { cache: true }
  }).then(response => response._embedded[type].map(group => {
    return formatGroup(group, { includePlacementSlots: false });
  }));
}

export function loadGroup(type, id, includes) {
  return request(`/api/chorus/${type}s/${id}`, {
    query: {
      community_id: app.state.get(['doc', 'community_id']),
      includes: includes
    }
  }).then(formatGroup);
}

function formatGroup(group, { includePlacementSlots = true } = {}) {
  var groupData = pick(group, ENTRY_GROUP_ATTRIBUTES);
  var hubPageData = getPath(group, ['_embedded', 'hub_page']) || {};
  groupData.hub_page_id = hubPageData.id || null;
  if (includePlacementSlots) {
    groupData.placement_slots = hubPageData.placement_slots || [];
  } else {
    groupData.placement_slots = null;
  }

  return groupData;
}

export function loadNestedGroups(type) {
  var result = {};
  return loadGroups(type).then(allGroups => {
    if (allGroups.length > 0) {
      var topLevelGroups = allGroups.filter(group => group.parent_entry_group_id == null);
      var alphabetizedGroups = groupByFirstChar(topLevelGroups);
      result = nestedGroupsList(alphabetizedGroups, allGroups);
    }
    return result;
  });
}

// Turn a single group into an array of group(s), including the initial group +
// any auto-added parent groups.
function getAllParents(group, allGroups) {
  if (group.parent_entry_group_id) {
    var parent = allGroups.find(g => g.id === group.parent_entry_group_id);
    if (parent && parent.auto_add_subgroup_entries) {
      return [group].concat(getAllParents(parent, allGroups));
    }
  }
  return [group];
}

function childrenOf(parent, allGroups) {
  var childrenOfParent = allGroups.filter(group => group.parent_entry_group_id === parent.id);
  var children = sortBy(childrenOfParent, 'name');
  parent.children = children;
  return children;
}

function getAllChildren(parent, allGroups, depth=0) {
  var children = childrenOf(parent, allGroups);
  parent.depth = depth;

  if (children.length > 0) {
    depth++;
    for (let child of children) {
      getAllChildren(child, allGroups, depth);
    }
  }
}

function nestedGroupsList(alphabetizedGroups, allGroups) {
  for (let topLevelGroups of Object.values(alphabetizedGroups)) {
    for (let topLevelGroup of topLevelGroups) {
      getAllChildren(topLevelGroup, allGroups);
    }
  }
  return alphabetizedGroups;
}

export function addGroup(allGroups, group, index, type) {
  var contents = app.state.get(['doc', 'head', 'contents']);
  var savedGroups = contents.draft[`${type}s`];
  var groupsToAdd = getAllParents(group, allGroups);

  // When adding a group, we first get a collection of groups to add that may include
  // parent groups, if the selected group auto-adds parents. We then iterate through
  // this list of groups, adding _only_ the groups that don't exist in state already.
  // This is important, since parent groups auto-added may already exist in state.
  var delta = groupsToAdd.reduce((ops, groupToAdd) => {
    if (!savedGroups.find(g => g.id === groupToAdd.id)) {
      ops.push({
        p: ['draft', `${type}s`, index],
        li: groupFromData(groupToAdd)
      });
    }
    return ops;
  }, []);

  autosave.localUpdate(app.state.get(['doc', 'id']), delta);
}

export function removeGroup(groupId, primaryGroupId, index, type) {
  var primaryGroupPath = ['draft', `primary_${type}_id`];
  var savedGroupsPath = ['draft', `${type}s`];
  var savedGroups = app.state.get(['doc', 'head', 'contents', ...savedGroupsPath]);
  var delta = [];

  // If the group to remove is the primary group, set the next group in state as
  // the primary group. If no other groups in state, simply remove the primary group id.
  if (groupId === primaryGroupId) {
    var newPrimaryGroup = savedGroups.find(g => g.id !== primaryGroupId);
    if (newPrimaryGroup) {
      delta.push({ p: primaryGroupPath, od: primaryGroupId, oi: newPrimaryGroup.id });
    } else {
      delta.push({ p: primaryGroupPath, od: primaryGroupId });
    }
  }

  // Always remove the group from state
  delta.push({ p: [...savedGroupsPath, index], ld: savedGroups[index] });
  autosave.localUpdate(app.state.get(['doc', 'id']), delta);
}

export function setPrimaryGroup(groupId, type) {
  var primaryGroupPath = ['draft', `primary_${type}_id`];
  var current = app.state.get(['doc', 'head', 'contents', ...primaryGroupPath]);
  var docId = app.state.get(['doc', 'id']);
  autosave.localUpdate(docId, [{ p: primaryGroupPath, od: current, oi: groupId }]);
}

// Transforms Chorus group data into a new Anthem schema compliant group for doc.head.contents
export function groupFromData(data) {
  // This config controls whether the default placement should be for the first
  // unpinned slot. Otherwise, the default is do not place.
  var unpinnedByDefault = app.state.get(['community', 'placeFirstAvailableDefault']);
  var group = pick(data, ['id', 'hub_page_id', 'name', 'slug']);
  group.placement_slot = unpinnedByDefault ? null : -1;

  return group;
}
