var request = require('../lib/request');
var groupByFirstChar = require('../lib/group_by_first_char');

export function loadLinkSets() {
  return request('/api/chorus/link_sets', {
    query: {
      per_page: 10000,
      community_id: app.state.get('doc', 'community_id')
    },
    opts: { cache: true }
  }).then(function(response) {
    return response._embedded.link_set;
  });
}

export function loadAlphabetizedLinkSets() {
  return loadLinkSets().then(linkSets => groupByFirstChar(linkSets));
}
