import Quill from 'quill';

const Parchment = Quill.import('parchment');

export default class BooleanDataAttributor extends Parchment.Attributor.Attribute {
  constructor(name, options) {
    super(name, `data-${name}`, options);
  }

  canAdd(node, value) {
    return value === true && super.canAdd(node, value);
  }

  add(node, value) {
    if (this.canAdd(node, value)) {
      node.dataset[this.attrName] = '';
      return true;
    }
    return false;
  }

  value(node) {
    return node.dataset[this.attrName] === '';
  }

  remove(node) {
    delete node.dataset[this.attrName];
  }
}
