const { COMMUNITY_CHANGED } = require('../../config/messages');
const messageActions = require('../../actions/messages');
const getQueryParams = require('../../lib/get_query_params');

export default function initCommunityChangedNotice() {
  const params = getQueryParams();
  if (params.communityChanged) {
    const docLabel = app.state.get(['docLabel']);

    messageActions.showMessage({
      type: 'positive',
      message: COMMUNITY_CHANGED.replace('{docLabel}', docLabel)
    });
  }
}
