var autosave = require('../../../actions/autosave');

export default function initStructuredTags() {
  var community = app.state.get(['community']);
  var delta = [];
  var contents = app.state.get(['doc', 'head', 'contents']);

  if (!contents.autotag_league_id && community.autoTagging.leagueId) {
    delta.push({
      p: ['autotag_league_id'],
      oi: community.autoTagging.leagueId
    });
  }

  if (!contents.autotag_team_id && community.autoTagging.teamId) {
    delta.push({
      p: ['autotag_team_id'],
      oi: community.autoTagging.teamId
    });
  }

  // For each tag type, add an empty array for that tag type to state. For new
  // docs this will add all tag types to the doc. For existing docs, this will
  // add any missing tag types.
  for (let tagType of community.autoTagging.autotagTagTypes) {
    if (!contents.draft.structured_tags.hasOwnProperty(tagType)) {
      delta.push({
        p: ['draft', 'structured_tags', tagType],
        oi: []
      });
    }
  }

  if (delta.length > 0) {
    autosave.localUpdate(app.state.get(['doc', 'id']), delta, -1);
  }
}
