// join an array by:
//   inserting ',' between each element pair, except the last
//   inserting ', and' between the last pair
export default function commaJoin(array) {
  let str = '';
  for (let [i, item] of array.entries()) {
    if (i > 0 && array.length > 2) {
      str += ','; // including the oxford comma
    }
    str += ' ';
    if (i > 0 && i === array.length - 1) {
      str += 'and ';
    }
    str += item;
  }
  return str;
}
