import Quill from 'quill';

const Inline = Quill.import('blots/inline');

class Strike extends Inline {
  // Use a simple boolean, despite tagName being an array,
  // which by default causes this to return: { strike: 'S' }
  static formats() {
    return true;
  }

  // Ensure that the <s> tag is used, not <strike>
  optimize(context) {
    super.optimize(context);
    if (this.domNode.tagName !== 'S') {
      this.replaceWith('strike');
    }
  }
}

Strike.blotName = 'strike';

// Support parsing both <s> and <strike> from pasted HTML
Strike.tagName = ['S', 'STRIKE'];

Quill.register('formats/strike', Strike);

export default Strike;
