var { remoteUpdate, onReconnect, updateDocFlag } = require('../../actions/autosave');
var messageActions = require('../../actions/messages');
var messages = require('../../config/messages');
var stateMatchObserver = require('../../lib/state_match_observer');

export default function initAutosave() {
  app.state.set('autosave', {
    dirty: {},
    inLine: {},
    inFlight: {},
    sentIds: {},
    quillContentPaths: {},
    reconnectingIds: {},
    initializingIds: {},
    docsById: {}
  });

  // Receive remote update packets from the server and apply locally
  app.contentApiSocket.on('autosave.remote-update', remoteUpdate);

  // Receive status changes from the server and apply locally
  app.contentApiSocket.on('autosave.doc-update', (docId, path, value) => {
    if (path.includes('schema_version')) {
      messageActions.showMessage({ type: 'positive', message: messages.RELOAD_REQUIRED, forceRefresh: true });
      stateMatchObserver.stopAll();
    }
    updateDocFlag(docId, path, value);
  });

  app.on('autosave.error', () => messageActions.showError(messages.REALTIME_ERROR));
  app.once('autosave.error', err => {
    // Stop receiving remote updates, they may cause unwanted duplicate
    // reports of 'missed a sequence' errors
    app.contentApiSocket.off('autosave.remote-update', remoteUpdate);

    // Store the error in app state to signal read-only mode
    app.state.set(['autosave', 'error'], err);
  });

  app.on('reconnect', onReconnect);
}
