import Quill from 'quill';

const Inline = Quill.import('blots/inline');

class Italic extends Inline {
  // Use a simple boolean, despite tagName being an array,
  // which by default causes this to return: { italic: 'EM' }
  static formats() {
    return true;
  }

  // Ensure that the <em> tag is used, not <i>
  optimize(context) {
    super.optimize(context);
    if (this.domNode.tagName !== 'EM') {
      this.replaceWith('italic');
    }
  }
}

Italic.blotName = 'italic';

// Support parsing both <em> and <i> from pasted HTML
Italic.tagName = ['EM', 'I'];

Quill.register('formats/italic', Italic);

export default Italic;
