import uuid from 'uuid';
import Quill from 'quill';

const Parchment = Quill.import('parchment');

function numParents() {
  let num = 0;
  let win = window;
  while (window && win !== window.top) {
    num += 1;
    win = win.parent;
  }
  return num;
}

class ObjectEmbed extends Parchment.Embed {
  static create(value) {
    let node = super.create(value);
    node.dataset.object = JSON.stringify({ [this.blotName]: value });
    node.dataset.frameId = uuid.v4();
    node.allowFullscreen = true;
    node.src = `/object-v2.html?parents=${numParents()}`;
    return node;
  }

  static value(domNode) {
    try {
      if (domNode.dataset.object) {
        return JSON.parse(domNode.dataset.object)[this.blotName];
      }
    } catch (err) {
      return undefined;
    }
  }

  static get className() {
    return `${this.blotName}-object`;
  }
}

ObjectEmbed.blotName = 'abstract';
ObjectEmbed.tagName = 'iframe';

export default ObjectEmbed;
