let promptReload = null;

export default function initReloadTab() {
  promptReload = startTimer();

  // Various events that indicate activity has happened / tab isn't stale
  window.addEventListener('keypress', resetTimer, false);
  window.addEventListener('click', resetTimer, false);
  app.on('autosave.local-update', resetTimer);
  app.on('autosave.remote-update', resetTimer);
}

function startTimer() {
  // When a tab becomes stale, trigger a socket disconnect
  return setTimeout(() => {
    app.emit('socket.disconnect', 'stale-tab');
  }, app.config.sockets.inactivePeriod);
}

function resetTimer() {
  clearTimeout(promptReload);
  promptReload = startTimer();
}
