import Quill from 'quill';
import { Delta } from 'rich-text';
import { bindAll } from 'lodash';

// Remove default tab and enter key behavior to act more like a text input,
// and convert newlines into spaces on text-change, except for the trailing newline.
class InlineMode {
  constructor(quill) {
    bindAll(this, ['setTabbing', 'unsetTabbing']);
    this.tabbing = false;

    // Listen to know when quill.root was focused via the <Tab> key
    document.addEventListener('keydown', this.setTabbing);
    document.addEventListener('keyup', this.unsetTabbing);

    // If quill.root was focused as a result of the <Tab> key, then pre-select
    // the full contents, mimicking native <input type="text"> behavior.
    quill.root.addEventListener('focus', () => {
      if (this.tabbing) {
        quill.setSelection(0, quill.getLength(), 'user');
      }
    });

    // Replace all default <Tab> keybindings with one that moves focus to the
    // next (or previous) element by delegating to the native behavior.
    quill.keyboard.bindings['Tab'] = [{ // eslint-disable-line dot-notation
      key: 'Tab',
      shiftKey: null,
      handler() {
        return true;
      }
    }];

    // Replace all default <Enter> keybindings with one that blurs the focus
    // and prevents any default behavior.
    quill.keyboard.bindings['Enter'] = [{ // eslint-disable-line dot-notation
      key: 'Enter',
      shiftKey: null,
      handler() {
        quill.setSelection(null, 'user');
        return false;
      }
    }];

    // Convert newlines to spaces on each change that adds a newline
    quill.on('debounced-text-change', (delta, oldContents, source) => {
      if (source !== 'user' || !delta.ops.some(op => /\n/.test(op.insert))) {
        return;
      }

      const fixDelta = new Delta();
      const contents = quill.getContents();
      const lastOp = contents.ops[contents.ops.length - 1];

      for (let op of contents.ops) {
        if (typeof op.insert === 'string') {
          let insert = op.insert;
          if (op === lastOp) {
            // dont count the trailing newline
            insert = insert.slice(0, -1);
          }
          let index = insert.indexOf('\n');
          while (index >= 0) {
            fixDelta.retain(index);
            fixDelta.insert(' ', op.attributes);
            fixDelta.delete(1);
            insert = insert.slice(index + 1);
            index = insert.indexOf('\n');
          }
          fixDelta.retain(insert.length);
        } else {
          fixDelta.retain(1);
        }
      }

      if (fixDelta.chop().length() > 0) {
        quill.updateContents(fixDelta, 'user');
      }
    });
  }

  setTabbing() {
    this.tabbing = true;
  }

  unsetTabbing() {
    this.tabbing = false;
  }

  teardown() {
    document.removeEventListener('keydown', this.setTabbing);
    document.removeEventListener('keyup', this.unsetTabbing);
  }
}

Quill.register('modules/inline-mode', InlineMode);

export default InlineMode;
