const { get: getPath } = require('lodash');
const autosave = require('../../../actions/autosave');
const userActions = require('../../../actions/users');
const toRichText = require('../../../../shared/to_rich_text');

export default async function setAutotweets() {
  let autotweets = app.state.get(['doc', 'head', 'contents', 'draft', 'autotweets']);
  const userId = app.state.get(['doc', 'head', 'contents', 'draft', 'contributors', 0, 'chorus_id']);
  const userHandle = userId && !getPath(autotweets, [1, 'handle']) ? await userActions.getTwitterUsername(userId) : null;
  const community = app.state.get(['community']);
  let delta = [];

  if (!autotweets) {
    autotweets = [{}, {}];
    delta.push({ p: ['draft', 'autotweets'], oi: autotweets });
  }

  for (let [index, context] of ['community', 'user'].entries()) {
    let tweet = autotweets[index];

    if (!tweet) {
      tweet = {};
      delta.push({ p: ['draft', 'autotweets', index], li: tweet });
    }

    const defaultTweet = {
      context,
      handle: context === 'community' ? community.twitterScreenName : userHandle,
      selected: community.twitterSendDefault,
      image: community.tweetWithImageByDefault,
      body: toRichText('')
    };
    for (let [key, value] of Object.entries(defaultTweet)) {
      if (tweet[key] == null && value != null) {
        delta.push({ p: ['draft', 'autotweets', index, key], oi: value });
      }
    }
  }

  if (delta.length > 0) {
    autosave.localUpdate(app.state.get(['doc', 'id']), delta, -1);
  }
}
