const Delta = require('rich-text').Delta;

const PRECEDES_OPEN_QUOTE = ['', ' ', '\n', '{', '[', '(', '<'];
const PRECEDES_OPEN_DOUBLE_QUOTE = PRECEDES_OPEN_QUOTE.concat(['']);
const PRECEDES_OPEN_SINGLE_QUOTE = PRECEDES_OPEN_QUOTE.concat(['']);

// Returns a new delta where all "dumb quotes" have been converted into "smart quotes".
// Whether to convert to an open or closed quote depends on the preceeding char
// (open quotes are only used following certain characters, including whitespace)
module.exports = function convertQuotes(delta) {
  let newDelta = new Delta();

  for (let [opIndex, op] of delta.ops.entries()) {
    // Don't convert non-strings
    if (typeof op.insert !== 'string') {
      newDelta.push(op);
      continue;
    }

    // Store the last character of the previous op for use below
    let prevOpLastChar = '';
    if (opIndex > 0) {
      let prevOp = delta.ops[opIndex - 1];
      prevOpLastChar = (typeof prevOp.insert === 'string')
        ? prevOp.insert[prevOp.insert.length - 1]
        : '';
    }

    // Examine each string char and replace open/closing single/double quotes with
    // their smart quote equivalents
    let converted = '';
    for (let [charIndex, char] of op.insert.split('').entries()) {
      // If this is the first char of the current op, use the last char of the
      // previous op
      let prevChar = charIndex === 0 ? prevOpLastChar : converted[charIndex - 1];
      if (char === '"') {
        char = PRECEDES_OPEN_DOUBLE_QUOTE.includes(prevChar) ? '' : '';
      } else if (char === '\'') {
        char = PRECEDES_OPEN_SINGLE_QUOTE.includes(prevChar) ? '' : '';
      }

      converted += char;
    }

    newDelta.insert(converted, op.attributes);
  }

  return newDelta;
};
