import Quill from 'quill';
import { bindAll, isEqual } from 'lodash';
import allowlistFormats from '../../../../shared/delta/allowlist_formats';
import { toInlineHtml } from 'convert-rich-text';

class Placeholder {
  constructor(quill, options) {
    bindAll(this, 'onFocus', 'onBlur', 'onTextChange');
    this.quill = quill;
    this.quill.on('debounced-text-change', this.onTextChange);
    this.quill.root.addEventListener('focus', this.onFocus);
    this.quill.root.addEventListener('blur', this.onBlur);
    this.container = quill.addContainer('ql-placeholder');
    this.options = options;
    this.set(options.placeholder);
    this.onTextChange();
  }

  showPlaceholder() {
    return (this.quill.getText() === '\n') &&
      ((document.activeElement !== this.quill.root) || !document.hasFocus());
  }

  onFocus() {
    if (this.quill.getText() === '\n') {
      this.quill.setContents(this.placeholder, 'user');
    }
  }

  onBlur() {
    if (isEqual(this.quill.getContents().ops, this.placeholder.ops)) {
      this.quill.setContents([], 'user');
    }
    this.onTextChange();
  }

  // Only show the placeholder when the input is empty and unfocused
  onTextChange() {
    this.container.style.display = this.showPlaceholder() ? 'block' : 'none';
    if (!this.options.autosize) {
      this.matchSize();
    }
  }

  set(delta) {
    if (delta == null) {
      delta = { ops: [{ insert: '\n' }] };
    } else if (typeof delta === 'string') {
      delta = { ops: [{ insert: `${delta}\n` }] };
    }
    var formats = Object.keys(this.quill.scroll.whitelist).concat('author');
    this.placeholder = allowlistFormats(delta, formats);
    this.container.innerHTML = toInlineHtml(delta, formats);
    if (!this.options.autosize) {
      this.matchSize();
    }
  }

  matchSize() {
    var editor = this.quill.root;
    if (this.showPlaceholder()) {
      var editorStyle = getComputedStyle(editor);
      var borderWidth =
        parseInt(editorStyle.borderTopWidth, 10) +
        parseInt(editorStyle.borderBottomWidth, 10);
      editor.style.minHeight = this.container.scrollHeight + borderWidth + 'px';
    } else {
      editor.style.minHeight = '';
    }
  }

  destroy() {
    this.quill.off('debounced-text-change', this.onTextChange);
    this.quill.root.removeEventListener('focus', this.onFocus);
    this.quill.root.removeEventListener('blur', this.onBlur);
  }
}

Quill.register('modules/placeholder', Placeholder);

export default Placeholder;
